.sidebar {
    width: 30vw;
    &__holder {
        min-height: calc(100vh - 198px);
        background-color: #fff;
        border-radius: 10px;
        box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.5) ;
        padding: 15px;
    }
    &__title {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 15px;
    }
    &__name {
    }
    &__button {
        font-size: 20px;
    }
    &__input {
        display: flex;
        align-items: center;
        width: 100%;
        border-radius: 10px;
        background-color: #F1F1F1;
        background-image: url(../../../../public/img/icons/loop.svg);
        background-size: 20px;
        background-position: 10px;
        background-repeat: no-repeat;
        padding: 8px 15px 8px 40px;
        margin-bottom: 15px;
    }
    &__playlists {
        height: calc(100vh - 340px);
        overflow-y: scroll;
    }
    &__playlist {
    }
}