.header {
    &__holder {
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 84px;
        background-color: #fff;
        border-radius: 10px;
        box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.5) ;
        padding: 30px;
    }
    &__logo {
    }
    &__nav {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 50vw;
        min-width: max-content;
    }
    &__input {
        display: flex;
        height: 50px;
        width: 300px;
        background-image: url(../../../../public/img/icons/loop.svg);
        background-size: 25px;
        background-position: 20px;
        background-repeat: no-repeat;
        background-color: #F1F1F1;
        border-radius: 20px;
        padding: 12px 24px 12px 55px;
    }
    &__buttons {
        display: flex;
        align-items: center;
    }
    &__button {
        margin-left: 30px;
        &:first-child {
            border-radius: 50%;
            background-color: #646464;
            padding: 5px;
        }
        &:first-child img {
            width: 25px;
        }
        &:last-child img {
            width:45px;
        }
    }
    &__img {
        width: 30px;
    }
    button {
        background-color: #ffffff00;
    }
}
